<script>
  import { auth, googleProvider } from "../../conf/firebase";
  import GoogleLogin from "./GoogleLogin.svelte";
  import { validateEmail, validatePassword } from "../utils/helper";
  let showDialog = false;
  let isLoading = false;
  let email;
  let showPassword = false;
  $: emailValid = validateEmail(email);
  let password;
  $: passwordValid = validatePassword(password);
  let signupError;
  let repeatedPassword;
  let name;

  let submitted;
  function toggleShowPassword() {
    console.log("click");
    showPassword = !showPassword;
  }
  function signUp(event) {
    console.log("Signup called");
    isLoading = true;
    auth.createUserWithEmailAndPassword(email, password).then(() => {
      showDialog = false;
      isLoading = false;
    });
  }
</script>

<style>
  div.icon {
    z-index: 50;
  }
  .footerContainer {
    display: flex;
    align-items: center;
  }

  .modal-card {
    max-width: 440px;
  }

  .signinButton {
    margin-bottom: 0 !important;
  }

  .icon {
    pointer-events: initial !important;
    cursor: pointer;
  }
</style>

<div>
  <button
    class="button is-primary is-rounded"
    on:click={() => {
      showDialog = true;
    }}>
    <strong>Register</strong>
  </button>
  <div class="modal is-active" class:is-active={showDialog}>
    <div class="modal-background" />
    <div class="modal-card">
      <header class="modal-card-head">
        <span class="modal-card-title has-text-white">Register</span>

        <button
          class="button is-rounded"
          aria-label="continue"
          on:click={() => (showDialog = false)}>
          Continue
        </button>
      </header>
      <form
        id="register"
        on:submit={e => {
          e.preventDefault();
          submitted = true;
          if (emailValid && passwordValid) signUp();
        }}>
        <section class="modal-card-body">
          <div class="field">
            <div class="control has-icons-left">
              <input
                class="input"
                type="text"
                placeholder="Name"
                bind:value={name} />
              <span class="icon is-small is-left">
                <i class="fas fa-user" />
              </span>
            </div>
          </div>

          <div class="field">
            <div class="control has-icons-left">
              <input
                class="input"
                type="email"
                placeholder="Email"
                bind:value={email} />
              <span class="icon is-small is-left">
                <i class="fas fa-envelope" />
              </span>
            </div>
            {#if submitted && email && emailValid !== undefined && !emailValid}
              <p class="help is-danger">This email is invalid</p>
            {/if}
          </div>
          <div class="field">
            <div class="control has-icons-left has-icons-right">
              {#if showPassword}
                <input
                  class="input"
                  type="text"
                  placeholder="Password"
                  autocomplete="new-password"
                  bind:value={password} />
              {:else}
                <input
                  class="input"
                  type="password"
                  placeholder="Password"
                  autocomplete="new-password"
                  bind:value={password} />
              {/if}
              <span class="icon is-small is-left">
                <i class="fas fa-key" />
              </span>
              {#if showPassword}
                <span
                  class="icon is-small is-right"
                  on:click={toggleShowPassword}>
                  <i class="fas fa-eye" />
                </span>
              {:else}
                <span
                  class="icon is-small is-right"
                  on:click={toggleShowPassword}>
                  <i class="fas fa-eye-slash" />
                </span>
              {/if}
            </div>
            {#if submitted && password && passwordValid !== undefined && !passwordValid}
              <p class="help is-danger">This password is invalid</p>
            {/if}
          </div>
          {#if signupError}
            <p class="help is-danger">{signupError}</p>
          {/if}
        </section>
        <footer class="modal-card-foot">
          <div class="footerContainer">
            <span>Or register via</span>
            <GoogleLogin />
          </div>
        </footer>
      </form>
    </div>
  </div>

</div>
